@import url("https://fonts.googleapis.com/css?family=Amatic+SC");
form .form-input .input {
  position: relative;
}

form .form-input .validation {
  display: none;
}

form .form-input.form-error .validation {
  display: block;
  background: #ff3300;
  color: #fff;
  position: absolute;
  right: 8px;
  top: 9px;
  border-radius: 3px;
  padding: 3px 8px;
}

form button {
  border: none;
  background: #42afe3;
  color: #fff;
  padding: 10px 22px;
  border-radius: 3px;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-weight: 600;
  border: 3px solid #42afe3;
  font-size: 14px;
}

form button:disabled {
  opacity: 0.5;
}

form button:hover:enabled {
  background: #fff;
  color: #42afe3;
  border: 3px solid #42afe3;
}

.autocomplete {
  display: block;
  border: 1px solid #ddd;
  position: absolute;
  background: #fff;
  margin: -6px 0 0;
  padding: 0;
  list-style: none;
  color: black;
}

.autocomplete li {
  margin: 0;
  padding: 6px 8px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
}

.autocomplete li.highlight {
  background: #ddd;
}

.autocomplete li:hover {
  background: #eee;
}

.form-details {
  width: 95%;
  margin: 20px auto;
  background: #eee;
  border-radius: 7px;
  -webkit-transition: all 300ms ease-in-out;
  -o-transition: all 300ms ease-in-out;
  transition: all 300ms ease-in-out;
}

.form-details tr {
  border: 1px solid #fff;
}

.form-details td,
.form-details th {
  padding: 5px 10px;
  font-family: Roboto Condensed;
  font-size: 17px;
  -webkit-transition: all 300ms ease-in-out;
  -o-transition: all 300ms ease-in-out;
  transition: all 300ms ease-in-out;
}

.form-details th {
  font-weight: 600;
  border-right: 1px solid #fff;
}

.form-details td {
  font-weight: 500;
}

.wraptext {
  word-wrap: break-word;
}

.example-enter {
  opacity: 0.01;
}

.example-enter.example-enter-active {
  opacity: 1;
  -webkit-transition: opacity 500ms ease-in;
  -o-transition: opacity 500ms ease-in;
  transition: opacity 500ms ease-in;
}

.example-leave {
  opacity: 1;
}

.example-leave.example-leave-active {
  opacity: 0.01;
  -webkit-transition: opacity 300ms ease-in;
  -o-transition: opacity 300ms ease-in;
  transition: opacity 300ms ease-in;
}

